import server from '../../server.js';
//import uniFab from '@/components/uni-fab.vue';
import uniLoadMore from '@/components/uni-load-more.vue';
export default {
	data() {
		return {
			TabCur:0,
			postList:[],
			weikong:true
		}
	},
	onLoad:function(e){
		uni.showLoading({
			title: '获取数据中'
		});
		uni.request({
			method:'GET',
			url: 'https://api.angeli.top/post.php?type=getMyGive', //仅为示例，并非真实接口地址。
			data: {
				page:1,
				count:20,
				token:server.token
			},
			header: {
				'content-type': 'application/x-www-form-urlencoded',
				
			},
			success: (res) => {
				
				if(res.data.code!=="1"){
					uni.showToast({
						title: res.data.msg,
						position:'bottom',
						icon:'none',
						duration:2000,
						mask:true
					});
					this.weikong=true
				}else{
					
					this.postList=res.data.data
					this.weikong=false
				}
				
			},
			complete() {
				uni.hideLoading();
			}
		});
	},
	methods: {
		Like:function(postid,auid,give,index){
			if(give===true){
				var modea='del'
			}else{
				var modea='add'
			}
			uni.request({
				method:'GET',
				url: "https://api.angeli.top/post.php?type=Like", //仅为示例，并非真实接口地址。
				data: {
					fuid: auid,
					postid:postid,
					mode:modea,
					token:server.token
				},
				header: {
					'content-type': 'application/x-www-form-urlencoded',
					
				},
				success: (res) => {
					if(res.data.code=="1"){
						if(modea=='add'){
							this.postList[index][0].Give=true;
							uni.showToast({
								title: "种草成功！",
								position:'bottom',
								icon:'none'
							});
						}else{
							this.postList[index][0].Give=false;
							uni.showToast({
								title: "取消种草成功！",
								position:'bottom',
								icon:'none'
							});
						}
						
						this.$forceUpdate()
					}else{
						
						uni.showToast({
							title: "种草失败！",
							position:'bottom',
							icon:'none'
						});
					}
				},
				complete() {
					
				}
			});
		},
		getClass:function(id){
			uni.navigateTo({
				url: '../classPost/classPost?id='+id
			})
		},
		showImage(res,c){
			uni.previewImage({
				current:c,
				urls: res,
				longPressActions: {
				itemList: ['发送给朋友', '保存图片', '收藏'],
					success: function (res) {
						
						uni.showToast({
							title: '选中了第' + (res.tapIndex + 1) + '个按钮',
							position:'bottom',
							icon:'none'
						});
						console.log('选中了第' + (res.tapIndex + 1) + '个按钮');
						
					},
					fail: function (res) {
						console.log(res.errMsg);
					}    
				}
			})
		},
		tabSelect(e) {
			this.TabCur = e

		},
		getbieren:function(e){
			uni.navigateTo({
				url: '../i/bieren?auid='+e
			})
		},
		getpostinfo:function(id){
			uni.navigateTo({
				url: '../postinfo/postinfo?id='+id
			});
		}
	}
}
